import React, { useEffect } from "react";

const KakaoSharePage = () => {
  useEffect(() => {
    const kakaoKey = process.env.REACT_APP_KAKAO_API_KEY;

    // Kakao SDK 초기화
    if (window.Kakao && !window.Kakao.isInitialized()) {
      window.Kakao.init(kakaoKey);
      console.log("Kakao SDK initialized");
    }
  }, []);

  const shareToKakao = () => {
    // 카카오톡 공유 메시지 설정
    window.Kakao.Link.sendDefault({
      objectType: "feed",
      content: {
        title: "조유하 ❤️ 서승원의 결혼식",
        description: "12월 29일, 저희 결혼식에 초대합니다.",
        imageUrl: "https://raw.githubusercontent.com/ordinaryhumanTohappy/wedding/main/images/소개이미지.jpg",
        link: {
          webUrl: "https://example.com",
          mobileWebUrl: "https://example.com",
        },
      },
      buttons: [
        {
          title: "지도 보기",
          link: {
            webUrl: "https://map.example.com",
            mobileWebUrl: "https://map.example.com",
          },
        },
        {
          title: "모바일 청첩장 열기",
          link: {
            webUrl: "https://invitation.example.com",
            mobileWebUrl: "https://invitation.example.com",
          },
        },
      ],
    });
  };

  return (
    <div style={{ textAlign: "center", padding: "20px" }}>
      <h1>조유하 ❤️ 서승원의 결혼식</h1>
      <p>12월 29일, 저희 결혼식에 초대합니다.</p>
      <button
        onClick={shareToKakao}
        style={{
          padding: "10px 20px",
          backgroundColor: "#FEE500",
          border: "none",
          borderRadius: "5px",
          cursor: "pointer",
          fontSize: "16px",
          color: "#000",
        }}
      >
        카카오톡으로 공유하기
      </button>
    </div>
  );
};

export default KakaoSharePage;
