import React, { useState } from "react";
import kakaoIcon from "./icons/kakaomap_vertical_ko.png"; // 아이콘 이미지 경로
import naverIcon from "./icons/Map_Service_Icon.png";
import tmapIcon from "./icons/tmap_icon.jpg";

const MapLinks = ({ latitude, longitude, placeName }) => {
  const [isModalOpen, setModalOpen] = useState(false); // 모달 상태 관리
  // 서비스 별 URL 생성
  const kakaoUrl = `https://map.kakao.com/link/to/${placeName},${latitude},${longitude}`;
  const naverUrl = `nmap://navigation?dlat=${latitude}&dlng=${longitude}&dname=${placeName}`;
  const tmapUrl = `tmap://route?goalname=Location&goalx=${longitude}&goaly=${latitude}`;

  // 모달 열기/닫기 함수
  const toggleModal = () => {
    setModalOpen(!isModalOpen);
  };


  return (
    <div style={{ display: "flex",
      flexDirection: 'column',
      height:"100%", justifyContent:"flex-start", gap:"1em", paddingTop:"0.5em"

    }}>
      {/* Kakao Map */}

        <a href={kakaoUrl} target="_blank" rel="noopener noreferrer">
          <img
            src={kakaoIcon}
            alt="Kakao Map"
            style={{ width: "50px", height: "auto", cursor: "pointer" }}
          />
          
        </a>

      {/* Naver Map */}
      <a href={naverUrl} target="_blank" rel="noopener noreferrer">
        <img
          src={naverIcon}
          alt="Naver Map"
          style={{ width: "50px", height: "auto", cursor: "pointer" }}
        />
      </a>
      

      {/* TMap */}
      <a href={tmapUrl} target="_blank" rel="noopener noreferrer">
        <img
          src={tmapIcon}
          alt="TMap"
          style={{ width: "50px", height: "auto", cursor: "pointer" }}
        />
      </a>

      <button
        onClick={toggleModal}
        style={{
          width: "50px", height: "50px",
          fontSize: " clamp(15px, 1em, 40px)",
          cursor: "pointer",
          color:"rgba(0,0,0,0.9)",
          borderRadius:"3px",
          marginBottom: "1em",
          border: "1px solid rgba(145,136,57,0.3)", // border를 제거하고 box-shadow로 대체
          backgroundColor: "rgba(145,136,57,0)",
        }}
      >
        대중
        <br/>교통
      </button>

      {isModalOpen && (
        <div
          style={{
            position: "fixed",
            top: 0, left: 0,
            width: "100%", height: "100%",
            justifyContent: "center", alignItems: "center",
            backgroundColor: "rgba(0, 0, 0, 0.5)",
            display: "flex",
            zIndex: 1000,
          }}
        >
          <div
            style={{
              backgroundColor: "white",
              padding: "20px",
              borderRadius: "10px",
              width: "70%",
              fontSize:"1.3em",
              textAlign: "left",
              position: "relative",
            }}
          >

            {/* 모달 닫기 버튼 */}
            <button
              onClick={toggleModal}
              style={{
                position: "absolute",
                top: "10px",right: "10px",
                backgroundColor:"white",
                border: "none",
                fontSize: "18px",
                cursor: "pointer",
              }}>❌</button> 

            <span style={{fontSize:"1.3em", lineHeight:"1.4em", fontWeight:"bold"}}>
              🚌 대중교통 방문 방법
            </span>
            <br/>
            <br/> - 웨딩홀 순환 버스 (15분 간격)
            <br/>E마트 버스승강장에서 아름다운 웨딩홀 왕복 운행 
              

            {/* 지도 링크 */}
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                gap: "1em",
              }}
            >
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MapLinks;
