import React, { useEffect, useState } from 'react';
import myStyle from './StartPage.module.scss';
import { motion, AnimatePresence } from "framer-motion";

const StartPage = ({ handleStartClick, bkImgIn, bkImgOut }) => {
  const [showSecondImage, setShowSecondImage] = useState(true);
  const [showSecondImage2, setShowSecondImage2] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowSecondImage(false);
    }, 2000); // 3초 후에 이미지2가 서서히 배경으로 이동

    return () => clearTimeout(timer); // 타이머 해제
  }, []);

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowSecondImage2(false);
    }, 5000); // 5초 후에 이미지2가 사라짐

    return () => clearTimeout(timer); // 타이머 해제
  }, []);

  

  return (
    <div className={myStyle.imageContainer } onClick={handleStartClick}>
      {/* 이미지1 (아래쪽에 고정) */}
			<div className={myStyle.centerText}>
				{/* 결혼식 */}
				<div className={myStyle.top}>
					서승원
				</div>
				<div className={myStyle.center}>
				❤️
				</div>
				<div className={myStyle.bottom}>
					조유하
				</div>
			</div>
      
      <div className={myStyle.bk}>


      </div>
      <AnimatePresence>
        {showSecondImage && (
          <motion.img
            key="image2"
            src={bkImgIn}
            alt="Image 2"
            className={myStyle.image}
            initial={{ opacity: 1, zIndex: 2 }}
            animate={{ opacity: 0.1 }}
            transition={{ opacity: { duration: 2 }, zIndex: { delay: 2 } }}
            style={{ position: "absolute" }}
          />
        )}
      </AnimatePresence>
      <AnimatePresence>
        {showSecondImage2 && (
          <motion.img
            key="image1"
            src={bkImgOut}
            alt="Image 1"
            className={myStyle.image}
            initial={{ opacity: 1 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 2 }}
            style={{ zIndex: 1, position: "absolute" }}
          />
        )}
      </AnimatePresence>

    </div>
  );
};

export default StartPage;
