import React, { useState, useEffect } from "react";
import './App.css';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import KakaoSharePage from "./components/content/KakaoSharePage";
import Haeder from './components/Haeder'
import Main from './components/Main'
import Footer from './components/Footer'
import StartPage from './components/StartPage'

import image1 from './assets/images/사진_2.jpg';
import bkImgIn from './assets/images/배경.jpg';
import bkImgOut from './assets/images/사진_1.jpg';


function App() {
  const [loading, setLoading] = useState(true);
  const [loading2, setLoading2] = useState(true);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    const img = new Image();
    img.src = image1;
  }, []);

  useEffect(() => {
    // 일정 시간 후에 자동으로 로딩 해제
    const timer = setTimeout(() => {
      setLoading2(false);
    }, 2000); // 예: 5초 후 자동 로딩 해제
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    // 일정 시간 후에 자동으로 로딩 해제
    const timer = setTimeout(() => {
      setLoading(false);
    }, 7000); // 예: 5초 후 자동 로딩 해제
    return () => clearTimeout(timer);
  }, []);

  const handleStartClick = () => {
    setLoading(false); // 클릭 시 로딩 상태 해제
  };

  return (
    <div className="App">
      {loading ?
        (<StartPage onStartClick={handleStartClick} bkImgIn={bkImgIn} bkImgOut={bkImgOut} /> ):
        ("")}
      {!loading2 ?
      (<div className="container"> 
      <div
        className="backgroundOverlay"
        style={{
          backgroundImage: `url(${bkImgIn})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          maskImage:
            "linear-gradient(to top, rgba(150, 255, 150, 0.5) 25%, rgba(255, 255, 255, 0.5) 60%)",
          WebkitMaskImage:
            "linear-gradient(to bottom, rgba(150, 255, 150, 0.1) 25%, rgba(255, 255, 255, 0.1) 30%)",
        }}
      ></div>
        {/* 해더  */}
        <div className="haeder"> 
          <Haeder/>
        </div>
        {/* 데이터 출력창  */}
        <div className="main"> 
          <Main imageSrc={image1}/>
        </div>
        {/* 푸터  */}
        <div className="footer"> 
          <Footer/>
        </div>
      </div>):("")}
      <Router>
        <Routes>
          <Route path="/" element={<KakaoSharePage />} />
        </Routes>
      </Router>
    </div>
  
  );
}

export default App;
